import React from 'react';
import './CallLogListItem.css';
import { useDispatch } from 'react-redux';
import { sendCallInvitation } from '../slices/callSlice';

function CallLogListItem({ name, duration, time, type }) {
  const dispatch = useDispatch();

  const callFriend = () => {
    dispatch(sendCallInvitation(name));
  };

  const isRejected = () => {
    return type.endsWith('REJECTED');
  }

  const isIncoming = () => {
    return type.startsWith('INCOMING');
  }

  return (
    <div className="log-list-item">
      <div className="log-list-item__avatar" style={{ backgroundColor: isRejected() ? 'red' : '' }}>
        {isIncoming() ? <svg width="24" height="24" fill="none" viewBox="0 0 24 24">
          <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M8.89286 4.75H6.06818C5.34017 4.75 4.75 5.34017 4.75 6.06818C4.75 13.3483 10.6517 19.25 17.9318 19.25C18.6598 19.25 19.25 18.6598 19.25 17.9318V15.1071L16.1429 13.0357L14.5317 14.6468C14.2519 14.9267 13.8337 15.0137 13.4821 14.8321C12.8858 14.524 11.9181 13.9452 10.9643 13.0357C9.98768 12.1045 9.41548 11.1011 9.12829 10.494C8.96734 10.1537 9.06052 9.76091 9.32669 9.49474L10.9643 7.85714L8.89286 4.75Z"></path>
          <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M19.25 4.75L14.75 9.25"></path>
          <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M14.75 5.75V9.25H18.25"></path>
        </svg> : <svg width="24" height="24" fill="none" viewBox="0 0 24 24">
          <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M8.89286 4.75H6.06818C5.34017 4.75 4.75 5.34017 4.75 6.06818C4.75 13.3483 10.6517 19.25 17.9318 19.25C18.6598 19.25 19.25 18.6598 19.25 17.9318V15.1071L16.1429 13.0357L14.5317 14.6468C14.2519 14.9267 13.8337 15.0137 13.4821 14.8321C12.8858 14.524 11.9181 13.9452 10.9643 13.0357C9.98768 12.1045 9.41548 11.1011 9.12829 10.494C8.96734 10.1537 9.06052 9.76091 9.32669 9.49474L10.9643 7.85714L8.89286 4.75Z"></path>
          <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M19.25 4.75L14.75 9.25"></path>
          <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M19.25 8.25V4.75H15.75"></path>
        </svg>}
      </div>
      <div className="log-list-item__info">
        <h4 className="log-list-item__name">@{name}</h4>
        <div className="log-list-item__extra">
          {!isRejected() && <p className="log-list-item__dur">duration: {
            duration > 60 ? `${Math.round(duration / 60)}min` : `${Math.round(duration)}sec`
          }</p>}
          <p className="log-list-item__time">time: {time}</p>
        </div>
      </div>
      <button className="log-list-item__call-btn" onClick={callFriend}>
        <svg width="24" height="24" fill="none" viewBox="0 -1 23 23">
          <path stroke="white" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M8.89286 4.75H6.06818C5.34017 4.75 4.75 5.34017 4.75 6.06818C4.75 13.3483 10.6517 19.25 17.9318 19.25C18.6598 19.25 19.25 18.6598 19.25 17.9318V15.1071L16.1429 13.0357L14.5317 14.6468C14.2519 14.9267 13.8337 15.0137 13.4821 14.8321C12.8858 14.524 11.9181 13.9452 10.9643 13.0357C9.98768 12.1045 9.41548 11.1011 9.12829 10.494C8.96734 10.1537 9.06052 9.76091 9.32669 9.49474L10.9643 7.85714L8.89286 4.75Z"></path>
        </svg>
      </button>
    </div>
  )
}

export default CallLogListItem